import React, { useState } from 'react';
import { Link, graphql } from 'gatsby';
import Layout from "../components/layout"
import Hero from '../components/shared/hero';
import BottomCta from '../components/shared/bottom-cta';
import CollapsibleSection from '../components/shared/collapsable';
import ScrollAnimation from 'react-animate-on-scroll';
import VerticalSteps from '../components/shared/vertical-steps'

import '../styles/home.css';
import SEO from '../components/seo';
import constants from './../constants/home'
import packages from './../constants/packages'

import leftImageSteps from '../images/home/vertical-steps.svg'
import PackageCta from '../components/home/package-cta';
import ImageSection from '../components/home/image-section';
import PackageSection from '../components/home/package-section';
import ChannelsSection from '../components/home/channels-section';
import ReviewsSection from '../components/home/reviews-section';
import HelpCardsSection from '../components/home/help-cards-section';
import FormPopup from '../components/shared/form-popup';
import InternetPackage from '../components/utils/InternetPackage';
import TvPackage from '../components/utils/TvPackage';
import meta from "../constants/meta-descriptions";

const packageSize = (i) => {
  switch (i) {
    case 0:
    case 3:
      return 'mid';
    case 1:
    case 4:
      return 'big';
    case 2:
    case 5:
      return 'xl';
  }
}

const HomePage = ({ data }) => {
  const [showForm, setShowForm] = useState(false);
  
  const internetPackages = data.homePackages.edges
    .filter((node) => node.node.package === 'Само интернет')
    .map(({ node }, index) => {
      return new InternetPackage(node.packageid, packageSize(index), node.speed, node.price, { ...node });
    });

  const tvPackages = data.homePackages.edges
    .filter((node) => node.node.package !== 'Само интернет')
    .map(({ node }, index) => {
      return new TvPackage(node.packageid, packageSize(index), node.speed, node.price, { ...node }, node.package, '', node.tvcount);
    });

  return (
    <>
      <div className='home-page'>
        <Layout
          activeTab={'/za-doma'}
        >
          <SEO
            title={meta.home.title}
            description={meta.home.metaDescription}
          />
          <Hero
            heroImage={data.heroImage.childImageSharp.fluid.src}
            imageClass='home-packages-hero'
            title={(<h1 className='text-white mt-3'>Нашите пакети – по-добри<br />от тези под коледната елха</h1>)}
          />

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <section id='packages-hero' className='spacing-bottom'>
              <PackageSection
                enableToggle={true}
                combinedPackages={tvPackages}
                internetPackages={internetPackages}
              />
            </section>
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <PackageCta
              bgImg={data.bgImage.childImageSharp.fluid.src}
            >
              <p className='mb-md-5 mb-3 text-white'>Търсиш нещо по-така?</p>
              <h2 className='mb-md-5 mb-2 text-white'>Създай пакет по свой <br />образ и (без)подобие!</h2>
              <Link to={'/konfigurator'} className='btn btn-primary green mt-5 mt-md-0'>Създай свой пакет</Link>
            </PackageCta>
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ImageSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ChannelsSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <HelpCardsSection />
          </ScrollAnimation>


          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <BottomCta
              bgColor={'transparent'}>
              <h2 className='mb-4 d-none d-md-block'>Нямаме търпение да ти покажем<br /><span className='text-purple'> колко сме добри!</span></h2>
              <h2 className='mb-4 d-block d-md-none'>Нямаме търпение да ти покажем<span className='text-purple'> колко сме добри!</span></h2>
              <p className='mb-4'>Нашите пакети – по-благи от тези под коледната елха</p>
              <Link to={'/za-doma'} className='btn btn-primary green'>Виж пакетите</Link>
            </BottomCta>
          </ScrollAnimation>


          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <VerticalSteps
              title={<h2>Трите крачки към<br />NetSurf удоволствието</h2>}
              isDark={false}
              steps={constants.verticalSteps}
              leftImage={leftImageSteps}
            />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <ReviewsSection />
          </ScrollAnimation>

          <ScrollAnimation
            animateIn='fadeIn'
            animateOnce={true}
          >
            <CollapsibleSection
              isDark={false}
              faqs={constants.faqs}
              sectionTitle={(
                <>
                  <h2 style={{ whiteSpace: 'pre-wrap' }}>Често задавани въпроси</h2>
                  <hr className='center' />
                </>
              )}
            />
          </ScrollAnimation>
        </Layout>

        <FormPopup
          show={showForm}
          setShow={setShowForm}
          isBusiness={false}
        />

      </div>

    </>
  )
}

export const query = graphql`
  query HomePageQuery {
    bgImage: file(relativePath: {eq: "home/cta-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 200) {
          src
        }
      }
    }
    heroImage: file(relativePath: {eq: "home/home-packages-hero.png"}) {
      childImageSharp {
        fluid(maxHeight: 800) {
          src
        }
      }
    }
    homePackages: allGoogleSheetDevOnlyRow(filter: {packageid: {in: [2, 3, 4, 16, 22, 28]}}) {
      edges {
        node {
          id
          packageid
          speed
          package
          tvcount
          duration
          price
          bonuspoints
          pausescuts
          discounts
          individuallity
          timeshift
          archive
          record
        }
      }
    }
  }`

export default HomePage;